.modal-dm{--transition-duration: 100ms;top:0;left:0;width:100%;height:100%;z-index:10000;display:flex;position:absolute;align-items:center;justify-content:center;background-color:rgba(var(--color-neutral-shadow-raw)/30%);will-change:opacity;border-radius:var(-radius-small)}.modal-dm__wrapper{width:100%;max-height:100%;padding:40px 0;overflow:auto;box-sizing:border-box;will-change:transform}.modal-dm__content{width:600px;min-width:600px;min-height:100px;margin:0 auto;background-color:var(--color-neutral-background);border-radius:5px;box-shadow:0 10px 30px rgb(var(--black-raw)/30%)}.modal-dm__header{display:flex;min-height:40px;align-items:center;justify-content:space-between;padding:16px 32px;box-sizing:content-box}.modal-dm__header_divided{border-bottom:1px solid var(--color-neutral-border)}.modal-dm__title{margin:0;margin-right:auto;font-size:20px;color:var(--color-neutral-content)}.modal-dm__body{padding:0 40px 32px;color:var(--color-neutral-content)}.modal-dm__body_bare{padding:0;height:100%;display:flex;flex-direction:column}.modal-dm__footer{padding:16px 32px;background:var(--color-neutral-surface);box-shadow:inset 0 1px 0 var(--black_5);text-align:center;font-size:14px;line-height:22px;border-bottom-left-radius:var(--corner-radius-small);border-bottom-right-radius:var(--corner-radius-small);overflow:hidden;color:var(--color-neutral-content-subtler)}.modal-dm_fullscreen .modal-dm__content{width:calc(100vw - 80px);height:calc(100vh - 80px)}.modal-dm_visible{opacity:0;transition:opacity var(--transition-duration) ease}.modal-dm_visible .modal-dm__wrapper{transform:scale(1.05);transition:transform var(--transition-duration) ease}.modal-dm.visible{opacity:1}.modal-dm.visible .modal-dm__wrapper{transform:none}.modal-dm.before-appear{opacity:0}.modal-dm.before-appear .modal-dm__wrapper{transform:scale(1.05)}.modal-dm.appear{opacity:1}.modal-dm.appear .modal-dm__wrapper{transform:scale(1)}.modal-dm.before-disappear{opacity:1}.modal-dm.before-disappear .modal-dm__wrapper{transform:scale(1)}.modal-dm.disappear{opacity:0}.modal-dm.disappear .modal-dm__wrapper{transform:scale(1.05)}